
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { getPointList, applyPointMoney,checkApplyPointMoney } from '@/api/user.js'
export default {
name: 'integralGet',
components: {
XInput,
Group,
XButton
},
data () {
return {
toast: false,
pointMoney: '',
Moneyshow:0,
inputpoint:'',
point: '',
rulestaus:false,
moneyrule:'',
rate: 100,
monebeyond:0,
shui:0,
}
},
methods: {
close () {
this.toast = false
},
changepoint(){
this.Moneyshow = this.getshui(this.pointMoney);
},

getshui(num){
if (num <= 800 )
{
this.shui = 0;
}
else
{
if (num <= 4000)
{
this.shui = (num - 800 )* 0.2;
}
else
{
if (num * 0.8 <= 20000)
{
this.shui = (num * 0.8 ) * 0.2 - 0;
}
else
{
if (num * 0.8 <= 50000)
{
this.shui = (num * 0.8 ) * 0.3 - 2000;
}
else
{
this.shui = (num * 0.8 ) * 0.4 - 7000;
}
}
}
}
this.shui = this.shui.toFixed(2);
return (num - this.shui).toFixed(2);
},
login_btn () {
if (this.pointMoney > this.point * 1 / this.rate || this.pointMoney == 0) {
this.$vux.toast.text('可提现积分数不足', 'middle')
return
}
if (this.pointMoney < this.monebeyond * 1 ) {
this.$vux.toast.text('单次提现金额须大于'+this.monebeyond+'元', 'middle')
return
}
this.inputpoint = (this.pointMoney * this.rate).toFixed(0);

const postData = {
point: this.inputpoint
}
checkApplyPointMoney(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.toast = true
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
surebtn () {
this.close()
const that = this
this.$router.push({path: '/cooperation', query: {point: that.inputpoint}})




/*
applyPointMoney(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.toast.text('申请提现成功', 'middle')
this.pointList()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})*/
},
allpoint () {

this.pointMoney = (this.point * 1 / this.rate).toFixed(2);
this.changepoint();
},
pointList () {
const postData = {}
const that = this
getPointList(postData)
.then(res => {
if (res.data.errorcode === 0) {
that.point = res.data.data.point
that.moneyrule = res.data.data.moneyrule
that.rate = res.data.data.rate
if (!that.rate)that.rate=0;
that.monebeyond = res.data.data.monebeyond
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
},
ruleClick () {
this.rulestaus = true
},
ruleclose () {
this.rulestaus = false
}

},
created () {
this.pointList()
}
}



